@use "../base.scss";

.heading {
  font-family: "PT Sans", "Arial", sans-serif;
  font-size: 14px;
  line-height: 21px;
  font-weight: 400;
  text-transform: uppercase;
  color: base.$text-gray;
}

.heading__inner {
  width: 270px;
  margin: 0 auto;
  text-align: center;

  @include base.tablet {
    width: 445px;
  }
}

.heading__inner--wide {
  @include base.tablet {
    width: 484px;
  }
}

.heading__title {
  font-size: 18px;
  line-height: 26px;
  font-weight: 700;
  color: base.$text-black;
}

.heading__title--accent {
  font-size: 21px;
}

.heading__subtitle {
  margin: 0;
}
